const data = [
    {
        img: "data:image/png;base64,",
        text: "Киікті қазақ киелі жануар деп есептейді. Ел арасында оларды атқан адам бақытсыздыққа ұшырайды деген сенім бар. Киіктің қаншалықты киелі екендігін кім білсін, бірақ өте ақылды және қандай табиғи ортада болмасын, тез бейімделіп кететін жануар екендігі анық. Ақбөкендердің басына қаншама зобалаң заман туғанмен, миллиондаған жылдар бойы тұқымын сақтап қала алды. Осының өзі ақбөкендердің өмірге өте бейімділігін, өсімталдығын аңғартады."
    },
    {
        img: "data:image/png;base64,",
        text: "Бірқазан қанаттарының ұзындығы бес метрден аса алатын құстар ғана емес - бұл үш шақырымға дейін ұша алатын құстар! Олар Антарктидадан басқа барлық континенттерді мекендейді. Бірқазан жердің барлық құстарымен салыстырғанда ең үлкен және кең ұмсыққа ие. Бұл тұмсықта үш кәдімгі балық сиуы болуы мүмкін."
    }
    ,

    {img: "data:image/png;base64,", text: "Қоқиқаз қалай қоректенетініне назар аударыңыз. Бастар еңкейтіліп, баяу қозғалады және адасады. Айналасында бірде-бір қамыс жоқ. Бұл судың тұзды екенін білдіреді, қамыс өспейді. Қоқиқаз қамысты ұнатпайды, бұл құс жыртқыш аңдар алыстан көрінетін таза жерлерді жақсы көреді."},
    {img: "data:image/png;base64,", text: "Көл шағаласының өмір салты. Бірнеше мыңға дейінгі топтарда ұя салады. Олар әр түрлі ішкі су қоймаларында, негізінен жазық жерлерде, ірі және қол жетпейтін таяз сулары бар шөгінділер мен қамыстармен, салдармен, аралдармен қоныстанады."},
    {img: "data:image/png;base64,", text: "Кіші Аққу Тундра аққуы - солтүстік жарты шарда мекендейтін барлық аққулардың ішіндегі ең кішісі - оның ұзындығы 115-146 см, қанаттарының жайылуы 170-195 см, салмағы 4-9,5 кг.  Кіші түрлері C. с. bewickii евразиялық аққуға всыртқы ұқсастығы бар, бірақ онымен салыстырғанда ол едәуір кіші, мойыны қысқа және контуры дөңгелектелген"}
    ];

